* {
    margin: 0;
    padding: 0;
    border: 0;
    box-sizing: border-box;
}

.app {
    padding: 4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100vh;
    width: 100vw;
    background-color: hsl(235, 21%, 11%);
}
.app-holder {
    height: 28rem;
    width: 28rem;
    margin-bottom: 4rem;    
}
.app-new-todo {
    width: 100%;
    display: flex;
    align-items: center;
    height: 2.5rem;
    background-color: hsl(235, 24%, 19%);
    border-radius: 5px;
    padding-left: 1rem;
}
.app-input {
    flex: 1;
    outline: hsl(235, 24%, 19%);
    border: none;
    font-size: 1rem;
    background-color: transparent;
    color: hsl(234, 11%, 52%);
    padding-left: 1rem;
}
.app-top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: 700;
    font-size: 18px;
    letter-spacing: 1.5rem;
    color: hsl(0, 0%, 98%);
}
.app-todo-list {
    background-color: hsl(235, 24%, 19%);
    margin-top: 2rem;
    border-radius: 5px;
    height: 90%;
}

.app-radio {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 1px solid hsl(233, 14%, 35%);
    display: flex;
    align-items: center;
    justify-content: center;
}
.app-radio svg {
    visibility: hidden;
}