.todo {
    display: flex;
    align-items: center;
    width: 100%;
}
.todo-row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    border-bottom: 1px solid hsl(233, 14%, 35%);
    padding: 1rem;
    color: hsl(234, 39%, 85%);
    font-size: 1rem;
}
.todo-radio, .todo-radio-checked {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 1px solid hsl(233, 14%, 35%);
    display: flex;
    align-items: center;
    justify-content: center;
}
.todo-radio:hover {
    border-color: hsl(220, 98%, 61%);
}

.todo-radio-checked {
    background: linear-gradient(to right, hsl(192, 100%, 67%), hsl(280, 87%, 65%));
}
.todo-text, .todo-text-checked {
    flex: 1;
    padding-left: 1rem;
}
.todo-text-checked {
    text-decoration: line-through;
    color: hsl(237, 14%, 26%);
}